.NavigationItems {
    margin: 0;
    padding: 0 3rem;
    font-size: 1.2rem;
    font-weight: 400;
    list-style: none;
    display: flex;
    flex-flow: column;
    align-items: center;
    height: 100%; 
    position: relative;
}

.NavigationItems a {  
    color: white;
    text-decoration: none;
    margin: 0 1rem;
    width: 100%;
    font-size: 1.4rem;
}

.NavigationItems li {
    width: 100%;
    padding: .5rem 3.5rem;
    margin-top: 1.5rem;
    border-radius: 2%;
    transform: translateY(0);
    position: relative;
}

.NavigationItems a:hover,
.NavigationItems a:active,
.NavigationItems a.active {
    border-bottom: 2px solid rgb(0, 160, 200);
    border-radius: 5%;
    padding-bottom: .5rem;
}

.NavigationItems li:active,
.NavigationItems li.active {
    transform: translateY(1px);
}

.Notification {
    position: absolute;
    top: 0rem;
    left: 5rem;
    font-weight: 700;
    background-color: white;
    box-shadow: 0 2px 2px rgba(0,0,0,.2);
    width: 1rem;
    height: 1rem;
    border-radius: 50%;  
}

.NotificationNumber {
    margin-block-start: .2rem;
    margin-block-end: 0rem;
    margin-inline-end: 0rem;
    font-size: .6rem;
    line-height: .6rem;

}
@media (min-width: 501px and max-width 768px) {
    .NavigationItems {
        padding: 0;  
    }
}

@media (min-width: 500px) {
    .NavigationItems{
        flex-flow: row;
        justify-content: flex-end;
    }
    .NavigationItems a {
        color:white;
        text-decoration: none;
        margin: 0 1rem;
        width: 100%;
        font-weight: 300;
    }
    
    .NavigationItems a:hover,
    .NavigationItems a:active,
    .NavigationItems a.active {
        padding-bottom: .5rem;
        color:white;
        border-bottom: 2px solid rgb(0, 160, 200);
        border-radius: 5%;
    }
    .NavigationItems li {
        padding: 0rem 0rem;
        box-shadow: none;
        border:none;
        padding: 0;
        margin-top: -.3rem;
        border-radius: 0;
    }
    .NavigationItems li:active,
    .NavigationItems li.active {
    box-shadow:  none;
    }

    .Notification {
        position: absolute;
        top: -5px;
        left: 3px;
        background-color: white;
        color: black;
        box-shadow: 0 2px 2px rgba(0,0,0,.2);
        width: 1rem;
        height: 1rem;
        border-radius: 50%;  
    }
}

@media (max-width: 375px) {
    .Notification {
        top: 0rem;
        left: 4.3rem;
    }
}
